<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->

    <!--  -->
    <el-card class="box-card">
      <!--   -->
      <el-row :gutter="20">
        <el-col :span="8">
          <el-input
            placeholder=""
            v-model="queryInfo.query"
            clearable
            @clear="getOrderList"
            @blur="searchOrderList"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="searchOrderList"
            ></el-button>
          </el-input>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="orderList" stripe style="width: 100%" border>
        <!--  -->
        <el-table-column type="index" label="#"> </el-table-column>
        <el-table-column prop="order_number" label="">
        </el-table-column>
        <el-table-column prop="order_price" label="" width="120px">
        </el-table-column>
        <el-table-column prop="pay_status" label="" width="120px">
          <template slot-scope="scope">
            <el-tag v-if="scope.row.pay_status === '0'" type="danger"
              ></el-tag
            >
            <el-tag v-else type="success"></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label="" width="120px">
        </el-table-column>
        <el-table-column label="" width="180px">
          <template slot-scope="scope">
            {{ scope.row.create_time | cqDateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="270px">
          <template>
            <el-button
              type="primary"
              icon="el-icon-edit"
              size="mini"
              @click="adressDialog = true"
              ></el-button
            >
            <el-button
              type="success"
              icon="el-icon-location"
              size="mini"
              @click="getProcessInfo"
              disabled
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!--  -->

    <!-- dialog -->
    <el-dialog
      title=""
      :visible.sync="adressDialog"
      width="50%"
      @close="resetAdress"
    >
      <el-form
        ref="addressForm"
        :model="addressForm"
        :rules="addressFormRules"
        label-width="80px"
      >
        <el-form-item label="" prop="address1">
          <!--   -->
          <el-cascader
            v-model="addressForm.address1"
            :options="cityData"
            clearable
            placeholder=""
            :props="{ expandTrigger: 'hover' }"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="addressForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="resetAdress"> </el-button>
        <el-button type="primary" @click="resetAdress"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="processDialog"
      width="50%"
    >
      <div class="radio">
        
        <el-radio-group v-model="reverse">
          <el-radio :label="true"></el-radio>
          <el-radio :label="false"></el-radio>
        </el-radio-group>
      </div>

      <el-timeline :reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in processInfo"
          :key="index"
          :timestamp="activity.time"
        >
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
// 
import cityData from './citydata.js'

export default {
  data() {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      // 
      orderList: [],
      // 
      total: 0,
      // 
      isSearch: false,
      // 
      cityData,
      // 
      addressForm: {
        // 
        address1: [],
        // 
        address2: ''
      },
      // 
      adressDialog: false,
      // 
      addressFormRules: {
        address1: [
          { required: true, message: '', trigger: 'blur' }
        ],
        address2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      processDialog: false,
      // 
      reverse: true,
      // 
      processInfo: []
    }
  },
  created() {
    // 
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList() {
      //  
      // 1
      if (this.queryInfo.query) {
        this.queryInfo.pagenum = 1
        // 
        this.isSearch = false
      }
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      // orderList
      console.log(res.data)
      this.orderList = res.data.goods
      // 
      this.total = res.data.total
    },
    // 
    searchOrderList() {
      // 
      this.isSearch = true
      // 
      this.getOrderList()
    },
    // 
    handleSizeChange(pageSize) {
      // 
      this.queryInfo.pagesize = pageSize
      this.getOrderList()
    },
    // 
    handleCurrentChange(pageNum) {
      // 
      this.queryInfo.pagenum = pageNum
      this.getOrderList()
    },
    // 
    resetAdress() {
      this.$refs.addressForm.resetFields()
      // 
      this.adressDialog = false
    },
    // 
    async getProcessInfo() {
      const { data: res } = await this.$http.get('/kuaidi/804909574412544580')
      if (res.meta.status !== 200) return this.$message.error('')
      this.processInfo = res.data
      this.processDialog = true
    }
  }
}
</script>

<style lang="less" scoped>
</style>
